import React from 'react'
import OqueRadar from '../../images/oqueradar.svg'
import HandShake from '../../images/handshake.svg'
import Target from '../../images/target.svg'
import Dollar from '../../images/dollar.svg'
import PlanosProponente from '../actioncomponents/PlanosProponente'
import PerguntasERespostas from '../actioncomponents/PerguntasERespostas'
import LeisIncentifo from '../../images/leisdeincentivo.svg'
import BuscaFiltros from '../../images/buscafiltrosimagem.svg'
import ResultadosRelevantes from '../../images/resultadosrelevantes.svg'
import RelatoriosDetalhados from '../../images/relatoriosdetalhados.png'
import DicasPersonalizadas from '../../images/dicaspersonalizadas.svg'
import Footer from "../Footer"
import Potencialize from "../Potencialize"

    class Proponente extends React.Component {

        constructor() {

            super()

            this.state = {

                

            }
            
            this.myDivToFocus = React.createRef()

         }

         componentWillMount() {

            window.scrollTo(0, 0)

        }

        componentDidMount() {
   
            this.props.location.exibePrecoIniciar && this.verPrecos()
   
        }  
     
         verPrecos = (event) => {
             
             if(this.myDivToFocus.current){

                 this.myDivToFocus.current.scrollIntoView({ 
                    behavior: "smooth", 
                    block: "start"
                 })
                 
             }
         }

        

        render() {

            return (

                <>

                    <div className="paginaPropoentesHeader">

                        <div className="paginaPropoentesHeaderTexto">

                            <div className="paginaPropoentesHeaderTextoTitulo">

                                Radar de patrocinadores

                            </div>

                            <div className="paginaPropoentesHeaderTextosubTitulo">

                                Saia do bvio. Otimize sua captao. Potencialize seu impacto social.

                            </div>

                            <div className="paginaPropoentesHeaderTextoBotao" onClick={this.verPrecos}>

                                Ver preos

                            </div>

                        </div>

                    </div>
                    
                    <div className="paginaPropoentesOQuee">

                        <div className="paginaPropoentesOQueeContainer">

                            <div className="paginaPropoentesOQueeConteudoEsquerdo">

                                <img alt="imagemOqueRadar" src={OqueRadar} />

                            </div>

                            <div className="paginaPropoentesOQueeConteudoDireito">

                                <div className="paginaPropoentesOQueeTitulo">

                                    O que  o radar de patrocinadores?

                                </div>

                                <div className="paginaPropoentesOQueeDescricao">

                                    Uma ferramenta que vai otimizar o processo de captao de recursos do seu projeto. Um sistema com informaes de mais de 62 mil CNPJs que j investiram em leis de incentivo. Com inteligncia de dados, indicamos quais patrocinadores mais se assemelham ao perfil do seu projeto. Veja tambm o histrico de investimentos destas empresas atravs de relatrios e monte propostas de patrocnio sob medida. Assim, voc investe seu tempo de forma mais otimizada.

                                </div>

                            </div>

                            <div className="paginaPropoentesOQueeConteudoEsquerdoMobile">

                                <img className="paginaPropoentesOQueeConteudoEsquerdoMobileTag" alt="imagemOqueRadar" src={OqueRadar} />

                            </div>

                        </div>

                    </div>

                    <div className="paginaPropoentesBeneficios">

                        <div className="paginaPropoentesBeneficiosConteudo">

                            <div className="paginaPropoentesBeneficiosTexto">

                                <div className="paginaPropoentesBeneficiosTitulo">

                                    Benefcios da plataforma

                                </div>

                            </div>

                            <div className="paginaPropoentesBeneficiosCard">

                                <div className="paginaPropoentesBeneficiosCardConteudo">

                                    <div className="paginaPropoentesBeneficiosCardImagem">

                                        <img alt="imgHandShake" src={HandShake} />

                                    </div>

                                    <div className="paginaPropoentesBeneficiosCardTitulo">

                                        Novos patrocinadores

                                    </div>

                                    <div className="paginaPropoentesBeneficiosCardDescricao">

                                        Tenha a visibilidade de uma base que contm mais de 62 mil patrocinadores em leis de incentivo.

                                    </div>

                                </div>

                            </div>

                            <div className="paginaPropoentesBeneficiosCard">

                                <div className="paginaPropoentesBeneficiosCardConteudo">

                                    <div className="paginaPropoentesBeneficiosCardImagem">

                                        <img alt="imgTarget" src={Target} />

                                    </div>

                                    <div className="paginaPropoentesBeneficiosCardTitulo">

                                        Propostas assertivas

                                    </div>

                                    <div className="paginaPropoentesBeneficiosCardDescricao">

                                        Utilize dados para montar propostas feitas sob medida, baseada no histrico de investimento dos potenciais patrocinadores.

                                    </div>

                                </div>

                            </div>

                            <div className="paginaPropoentesBeneficiosCard">

                                <div className="paginaPropoentesBeneficiosCardConteudo">

                                    <div className="paginaPropoentesBeneficiosCardImagem">

                                        <img alt="imgDollar" src={Dollar} />

                                    </div>

                                    <div className="paginaPropoentesBeneficiosCardTitulo">

                                        Eficincia na captao

                                    </div>

                                    <div className="paginaPropoentesBeneficiosCardDescricao">

                                        Otimize a sua captao de recursos. Veja que empresas investem em projetos parecidos com o seu.

                                    </div>

                                </div>

                            </div>

                        </div>

                    </div>

                    <div className="paginaPropoentesLeisDeIncentivo">

                        <div className="paginaPropoentesLeisDeIncentivoConteudo">

                            <div className="paginaPropoentesLeisDeIncentivoTexto">

                                <div className="tituloSecaoPaginaProponente">

                                    Leis de incentivo

                                </div>

                                <div className="barraSeparacao" />

                                <div className="subtituloSecaoPaginaProponente">

                                    Tenha a visibilidade do histrico de investimentos dos ltimos 3 anos na Lei Rouanet e na Lei de Incentivo ao Esporte (Federal). Nossos dados vm de bases pblicas e so atualizados em tempo real. Sempre tem informao nova saindo do forno.

                                </div>

                            </div>

                            <div className="paginaPropoentesLeisDeIncentivoImagem">

                                <img alt="leisIncentivoImagem" className="paginaPropoentesLeisDeIncentivoImagemTag" src={LeisIncentifo} />

                            </div>

                        </div>

                    </div>

                    <div className="paginaPropoentesBuscaFiltro">

                        <div className="paginaPropoentesBuscaFiltroConteudo">

                            <div className="paginaPropoentesBuscaFiltroImagem">

                                <img className="paginaPropoentesBuscaFiltroImagemTag" alt="BuscaFiltroImagem" src={BuscaFiltros} />

                            </div>

                            <div className="paginaPropoentesBuscaFiltroTexto">

                                <div className="tituloSecaoPaginaProponente">

                                    Busca por fitros

                                </div>

                                <div className="barraSeparacao" />

                                <div className="subtituloSecaoPaginaProponente">

                                    62 mil CNPJs  muita coisa. Mas fique tranquilo, nossos filtros esto aqui pra isso! Voc pode utilizar 9 diferentes filtros para descrever o seu projeto e obter os melhores resultados.

                                </div>

                            </div>

                        </div>

                    </div>

                    <div className="paginaPropoentesResultadosRelevantes">

                        <div className="paginaPropoentesResultadosRelevantesConteudo">

                            <div className="paginaPropoentesResultadosRelevantesTexto">

                                <div className="tituloSecaoPaginaProponente">

                                    Resultados relevantes

                                </div>

                                <div className="barraSeparacao" />

                                <div className="subtituloSecaoPaginaProponente">

                                    Uma vez que os filtros foram selecionados, nossos algoritmos comeam a trabalhar. Com inteligncia de dados, conseguimos encontrar as empresas mais relevantes para o seu projeto baseado em seus histricos de investimento.

                                </div>

                            </div>

                            <div className="paginaPropoentesResultadosRelevantesImagem">

                                <img alt="ResultadosRelevantesImagem" className="paginaPropoentesResultadosRelevantesImagemTag" src={ResultadosRelevantes} />

                            </div>

                        </div>

                    </div>

                    <div className="paginaPropoentesBuscaFiltro">

                        <div className="paginaPropoentesBuscaFiltroConteudo">

                            <div className="paginaPropoentesBuscaFiltroImagem">

                                <img className="paginaPropoentesBuscaFiltroImagemTag" alt="BuscaFiltroImagem" src={RelatoriosDetalhados} />

                            </div>

                            <div className="paginaPropoentesBuscaFiltroTexto">

                                <div className="tituloSecaoPaginaProponente">

                                    Relatrios detalhados

                                </div>

                                <div className="barraSeparacao" />

                                <div className="subtituloSecaoPaginaProponente">

                                    Achou AQUELA empresa? Clique nela e saiba ainda mais sobre como ela costuma investir em leis de incentivo. Elaboramos um relatrio detalhado com informaes relevantes para que voc possa montar uma proposta feita sob medida.

                                </div>

                            </div>

                        </div>

                    </div>

                    <div className="paginaPropoentesDicasPersonalizadas">

                        <div className="paginaPropoentesDicasPersonalizadasConteudo">

                            <div className="paginaPropoentesResultadosRelevantesTexto">

                                <div className="tituloSecaoPaginaProponente">

                                    Dicas personalizadas

                                </div>

                                <div className="barraSeparacao" />

                                <div className="subtituloSecaoPaginaProponente">

                                    No entendeu o que fazer com as informaes do relatrio? Criamos dicas personalizadas baseadas no perfil de investimento da empresa. Assim como "O Poderoso Chefo" voc vai montar uma proposta irrecusvel.

                                </div>

                            </div>

                            <div className="paginaPropoentesResultadosDicasPeronalizadas">

                                <img alt="PeronalizadasImagem" className="paginaPropoentesResultadosDicasPeronalizadasTag" src={DicasPersonalizadas} />

                            </div>

                        </div>

                    </div>

                    <div ref={this.myDivToFocus}>

                        <PlanosProponente />

                    </div>

                    <PerguntasERespostas />

                    <Potencialize />
                    
                    <Footer />

                </>

            )

        }

    }

export default Proponente;