import React from 'react'
import { Link } from "react-router-dom";
import Imagem1 from '../../images/plataformaProponente1.svg'
import Imagem2 from '../../images/plataformaProponente2.svg'
import Imagem3 from '../../images/plataformaProponente3.svg'
import Imagem4 from '../../images/plataformaProponente4.svg'

    class PlataformaProponentes extends React.Component {

        constructor(){

            super()

            this.state = {
                componenteAtivo: 'radar',
                imagem: Imagem1,
                titulo: 'Radar de patrocinadores',
                subtituloBold: 'Saia do bvio e busque novos patrocinadores!',
                subtitulo: 'Temos uma lista de 62 mil CNPJs que j investiram em leis de incentivo, ns te ajudamos a encontrar os que mais se aproximam do perfil do seu projeto.',
            }

        }

        mudaComponenteAtivo = (componente) => {

            componente === 'radar' &&

            this.setState({
                componenteAtivo: componente,
                imagem: Imagem1,
                titulo: 'Radar de patrocinadores',
                subtituloBold: 'Saia do bvio e busque novos patrocinadores!',
                subtitulo: 'Temos uma lista de 62 mil CNPJs que j investiram em leis de incentivo, ns te ajudamos a encontrar os que mais se aproximam do perfil do seu projeto.',
            })

            componente === 'cnpj' &&

            this.setState({
                componenteAtivo: componente,
                imagem: Imagem2,
                titulo: 'Busca por CNPJ',
                subtituloBold: 'J tem uma empresa em mente?',
                subtitulo: 'Voc pode tambm acessar o relatrio de investimento de empresas especficas, assim voc vai mais preparado pra reunio.',
            })

            componente === 'relatorios' &&

            this.setState({
                componenteAtivo: componente,
                imagem: Imagem3,
                titulo: 'Relatrios de Investimento',
                subtituloBold: 'Encontrou uma empresa interessante?',
                subtitulo: 'Veja o perfil de investimento dela nos ltimos 4 anos. Assim como "O Poderoso Chefo" voc vai conseguir montar uma proposta irrecusvel.',
            })

            componente === 'atualizacoes' &&

            this.setState({
                componenteAtivo: componente,
                imagem: Imagem4,
                titulo: 'Novas atualizaes',
                subtituloBold: 'Mantenha-se atualizado de novidades',
                subtitulo: 'Veja as movimentaes do mercado no momento que elas acontecem. Nossas informaes so integradas com as bases pblicas e atualizadas em tempo real.',
            })

        }

        render(){

            return (

                <div>

                    <div className="landingPlataformaProponentesHeaderContainer">

                        <div className="landingPlataformaProponentesHeader">

                            <div className="landingPlataformaProponentesHeaderIcone" 
                                style={{
                                    backgroundColor: this.state.componenteAtivo === 'radar' ? '#71CAD4' : '', 
                                    color: this.state.componenteAtivo === 'radar' ? '#FFF' : ''
                                }} 
                                onClick={() => this.mudaComponenteAtivo('radar')}>

                                RADAR DE PATROCINADORES

                            </div>

                            <div className="landingPlataformaProponentesHeaderIcone"
                                style={{
                                    backgroundColor: this.state.componenteAtivo === 'cnpj' ? '#71CAD4' : '', 
                                    color: this.state.componenteAtivo === 'cnpj' ? '#FFF' : ''
                                }} 
                                onClick={() => this.mudaComponenteAtivo('cnpj')}>

                                BUSCA POR CNPJ

                            </div>

                            <div className="landingPlataformaProponentesHeaderIcone"
                                style={{
                                    backgroundColor: this.state.componenteAtivo === 'relatorios' ? '#71CAD4' : '', 
                                    color: this.state.componenteAtivo === 'relatorios' ? '#FFF' : ''
                                }} 
                                onClick={() => this.mudaComponenteAtivo('relatorios')}>

                                RELATRIOS DE INVESTIMENTO

                            </div>

                            <div className="landingPlataformaProponentesHeaderIcone"
                                style={{
                                    backgroundColor: this.state.componenteAtivo === 'atualizacoes' ? '#71CAD4' : '', 
                                    color: this.state.componenteAtivo === 'atualizacoes' ? '#FFF' : ''
                                }} 
                                onClick={() => this.mudaComponenteAtivo('atualizacoes')}>

                                NOVAS ATUALIZAES

                            </div>

                        </div>

                    </div>

                    <div className="landingPlataformaProponentesConteudoContainerDesktop">

                        <div className="landingPlataformaProponentesConteudo">

                            <div className="landingPlataformaProponentesTextoPlataforma">

                                <div className="landingPlataformaProponentesTextoPlataformaBotaoMobile">
                                    
                                    Ver preos
                                    
                                </div>

                                <div className="landingPlataformaProponentesTitulo">

                                    {this.state.titulo}

                                </div>

                                <div className="landingPlataformaProponentesBarra" />

                                <div className="landingPlataformaProponentesSubtituloBold">

                                    {this.state.subtituloBold}

                                </div>

                                <div className="landingPlataformaProponentesSubtitulo">

                                    {this.state.subtitulo}

                                </div>

                                <Link className="landingPlataformaProponentesBotao" to={{pathname: "proponentes", exibePrecoIniciar: true}}>
                                    
                                    Ver preos
                                    
                                </Link>

                            </div>

                            <div className="landingPlataformaProponentesTextoImagem">

                                <img className="imagemPreviewPlataforma" alt="imagemPlataformaProponente" src={this.state.imagem} />

                            </div>

                        </div>

                    </div>

                    {

                        /*


                            SEPARAO DIV DESKTOP E DIV MOBILE

                        
                        */

                    }

                    <div className="landingPlataformaProponentesConteudoContainerMobile">

                        <div className="landingPlataformaProponentesConteudoContainer">

                            <div className="landingPlataformaProponentesConteudo">

                                <div className="landingPlataformaProponentesTextoPlataforma">

                                    <Link className="landingPlataformaProponentesTextoPlataformaBotaoMobile" to={{pathname: "proponentes", exibePrecoIniciar: true}}>
                                        
                                        Ver preos
                                        
                                    </Link>

                                    <div className="landingPlataformaProponentesTitulo">

                                        Radar de patrocinadores

                                    </div>

                                    <div className="landingPlataformaProponentesBarra" />

                                    <div className="landingPlataformaProponentesSubtituloBold">

                                        Saia do bvio e busque novos patrocinadores!

                                    </div>

                                    <div className="landingPlataformaProponentesSubtitulo">

                                        Temos uma lista de 62 mil CNPJs que j investiram em leis de incentivo, ns te ajudamos a encontrar os que mais se aproximam do perfil do seu projeto.

                                    </div>

                                    <div className="landingPlataformaProponentesBotao">

                                        Ver preos

                                    </div>

                                </div>

                                <div className="landingPlataformaProponentesTextoImagem">

                                    <img className="imagemPreviewPlataforma" alt="imagemPlataformaProponente" src={this.state.imagem} />

                                </div>

                            </div>

                        </div>



                        

                        <div className="landingPlataformaProponentesConteudoContainer">

                            <div className="landingPlataformaProponentesConteudo">

                                <div className="landingPlataformaProponentesTextoPlataforma">

                                    <div className="landingPlataformaProponentesTitulo">

                                        Busca por CNPJ

                                    </div>

                                    <div className="landingPlataformaProponentesBarra" />

                                    <div className="landingPlataformaProponentesSubtituloBold">

                                        J tem uma empresa em mente?

                                    </div>

                                    <div className="landingPlataformaProponentesSubtitulo">

                                        Voc pode tambm acessar o relatrio de investimento de empresas especficas, assim voc vai mais preparado pra reunio.

                                    </div>

                                    <div className="landingPlataformaProponentesBotao">

                                        Ver preos

                                    </div>

                                </div>

                                <div className="landingPlataformaProponentesTextoImagem">

                                    <img className="imagemPreviewPlataforma" alt="imagemPlataformaProponente" src={Imagem2} />

                                </div>

                            </div>

                            </div>



                                                    

                            <div className="landingPlataformaProponentesConteudoContainer">

                                <div className="landingPlataformaProponentesConteudo">

                                    <div className="landingPlataformaProponentesTextoPlataforma">

                                        <div className="landingPlataformaProponentesTitulo">

                                            Relatrios de Investimento

                                        </div>

                                        <div className="landingPlataformaProponentesBarra" />

                                        <div className="landingPlataformaProponentesSubtituloBold">

                                            Encontrou uma empresa interessante?

                                        </div>

                                        <div className="landingPlataformaProponentesSubtitulo">

                                            Veja o perfil de investimento dela nos ltimos 4 anos. Assim como "O Poderoso Chefo" voc vai conseguir montar uma proposta irrecusvel.

                                        </div>

                                        <div className="landingPlataformaProponentesBotao">

                                            Ver preos

                                        </div>

                                    </div>

                                    <div className="landingPlataformaProponentesTextoImagem">

                                        <img className="imagemPreviewPlataforma" alt="imagemPlataformaProponente" src={Imagem3} />

                                    </div>

                                </div>

                            </div>



                                                    

                            <div className="landingPlataformaProponentesConteudoContainer">

                                <div className="landingPlataformaProponentesConteudo">

                                    <div className="landingPlataformaProponentesTextoPlataforma">

                                        <div className="landingPlataformaProponentesTitulo">

                                            Novas atualizaes

                                        </div>

                                        <div className="landingPlataformaProponentesBarra" />

                                        <div className="landingPlataformaProponentesSubtituloBold">

                                            Mantenha-se atualizado de novidades

                                        </div>

                                        <div className="landingPlataformaProponentesSubtitulo">

                                            Veja as movimentaes do mercado no momento que elas acontecem. Nossas informaes so integradas com as bases pblicas e atualizadas em tempo real.

                                        </div>

                                        <div className="landingPlataformaProponentesBotao">

                                            Ver preos

                                        </div>

                                    </div>

                                    <div className="landingPlataformaProponentesTextoImagem">

                                        <img className="imagemPreviewPlataforma" alt="imagemPlataformaProponente" src={Imagem4} />

                                    </div>

                                </div>

                            </div>

                    </div>

                </div>

            )

        }

    }

export default PlataformaProponentes;